<template>
    <div>
        <div class="search">
            <input v-model="keyword" type="text" placeholder="输入城市名或拼音">
        </div>
        <div class="search-content" ref="search" v-show="keyword">
            <ul>
                <li class="search-item" v-for="item of list" :key="item.id">{{item.name}}</li>
                <!-- <li class="search-item" v-show="!list.length">没有找到匹配数据</li> -->
                <li class="search-item" v-show="hasNoData">没有找到匹配数据</li>
            </ul>
        </div>
    </div>

</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CitySearch",
  props:['cities'],
  data(){
      return {
          keyword:'',
          list:[],
          /* 定义一个截流函数 */
          timer:null
      }
  },
  computed:{
      hasNoData(){
          return !this.list.length;
      }
  },
  watch:{
      keyword(){
          if(this.timer){
              clearTimeout(this.timer);
          }
          if(!this.keyword){
              this.list = [];
              return
          }
          this.timer = setTimeout(()=>{
              const result = [];
              for(let i in this.cities){
                  this.cities[i].forEach(value=>{
                      /* 判断cities中是否包含这个关键词 */
                      if(value.spell.startsWith(this.keyword)|| value.name.startsWith(this.keyword)){
                          result.push(value);
                      }
                  })
              }
              this.list = result;
          },100)
      }
  },
  mounted(){
      this.scroll = new BScroll(this.$refs.search);
  }
};
</script>
<style lang="scss" scoped>
@import "style/global.scss";
.search {
  border-top: 1px solid rgba(250, 248, 248, 0.459);
  height: 88px;
  padding-left: 20px;
  padding-right: 20px;
  background: color(bg);
  @include flexCenter;
}
.search input {
  flex: 1;
  border-radius: 15px;
  border: none;
  outline: none;
  height: 66px;
  padding-left: 20px;
}
.search-content{
    position: absolute;
    left:0;
    right:0;
    bottom: 0;
    top:176px;
    z-index: 400;
    overflow: hidden;
    background: #fff;
    
}
.search-item{
    line-height: 62px;
    padding-left:20px;
    background: #fff;
    border-bottom: 1px solid #eee;
}
</style>

